import longButton from "../../components/my-components/LongButton";
let cache = '';
export default {
    data() {
        return {
            code:''
        }
    },
    components: {
        longButton
    },
    methods: {
        inputMoney(val) { //手动输入
            cache = val.detail.value;
        },
        submit() { //提交核销码
            this.pageLoading();
            this.Request('postSelfVerifyInfo', {
                order_id: '',
                finish_code: cache
            }).then(res => {
                this.closePageLoading();
            }).catch(res => {
                this.closePageLoading();
            })
        },
        checkVrCode() { //扫描获取二维码
            let that = this;
            uni.scanCode({
                success(res) {
                    cache = res.result;
                    this.code=cache;
                },
                fail(res) {
                    // console.log(res)
                }
            })
        },
        checkHistory() { //跳转历史核销记录
            uni.navigateTo({
                url: './historyRecord?from=selfVery'
            })
        }
    },
    onShow() {
        this.closePageLoading();
    }
};
